      <p>Pkuete d izbgn rizik i d s ztitit d nkivng. Puzdni ste k prfsinlc i k pritl, sv dk s n nt u kntrvrzn ili rizin situcii. Prihvtit pti trnd rzmilnj z ki prcnit d  rzumn. U principu vlit lud, li lit d prvrit d li su puzdni. Kd s s nkim pritn st, dt sv d sb d mu budt d pmi.</p>
      <p>Drugi Vs divlvu pr k nkg k prihvt incitivu, ng k inicitr. K nkg k  sdti n grdi dk n vidi u km prvcu vtr duv. K strtipng p ninu rzmilnj i k n b stimultivng.</p>
      <p>V mt  vrvtn: "Vi vlim d igrm n sigurn".</p>
    </div>
    <div class="vqAngazovan" v-if="(this.user.vqTest.result>=1)&&(this.user.vqTest.result<=14)">
