      <p>Drugi ludi  Vs vrvtn divti k lidr, k nkg s idm i inicitivm, ktivng i puzdng, znimlivg. d  Vs divti i k prvi zuztg, k nkg k s n prd zbvi ili putnju, k nkg kg s n tiu lin snj ili prblmi drugih ludi, k gistu.</p>
