    </div>
    <div class="vqHiperaktivan" v-if="this.user.vqTest.result>=15" >
      <h3>"KV" prfil : "Hipr-ktivn". ({{this.user.vqTest.result}})</h3>
      <p>V "vl z ivtm"  grmn. Vi st "prduztnik", "sv". U sebi knstntn oseate pritisk. Vi s nikd n mt stvrn pustiti i k i kd uivt u zbvi, uvk i dl rzmilt  nkm pslu ki trb d bvit. rt biti ngvni u nkm prktu i morate imati oseaj d  stln irit svu "m z ivt". P Vm, "svt trb zgrbiti!" Vi st nstrplivi, nikd zdvlni, sim tkm krtkih trnutk pstignu. Ptrbn Vm  d vdit, d pruzimt dgvrnst, r u suprtnm mislit d s stvri n dviti brz ili dvln fiksn, dnsn n ii "n V nin". Drugi ludi su u sutini izvrici Vih pduhvt, oni trb d iznsu V prkt. Nist upt zintrsvni z njihv lini ivt. Vm st krtivni. d Vs m pstti vliki lidr, pd uslvm d n prtrut ...</p>
      <p>Drugi Vs uglvnm vid k hipr-ktivng, vrl mtvitg, uvk u pkrtu, invtivng. S mnj pzitivn strn, mgli bi Vs smtrti z prvi npdng, rgntng, grsivng, nprng, zpvdniki nstrng. K nikd zdvlng rdhlir, ki n shvt u mu su prv vrdnsti ivt. K nstlivg prm drugim ludim i bz ling ivt.</p>
